.slide::before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 150%;
  background: -webkit-gradient(
    linear,
    left bottom,
    left top,
    from(rgba(0, 0, 0, 0.7)),
    to(transparent)
  );
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(transparent));
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.7), transparent);
  background: -moz-linear-gradient(rgba(0, 0, 0, 0.7), transparent);
  background: -o-linear-gradient(rgba(0, 0, 0, 0.7), transparent);
  background: linear-gradient(rgba(0, 0, 0, 0.7), transparent);
  bottom: 0;
  left: 0;
}

